import React, { ReactNode } from "react";
import * as auth from "authProvider";
import { User } from "types";
import { http } from "utils/http";
import { useMount } from "utils";
import { useAsync } from "utils/useAsync";
import { FullPageLoading } from "components/lib";
import { useQueryClient } from "react-query";

interface AuthForm {
  username: string;
  password: string;
}


const bootstrapUser = async () => {
  let user = null

  const token = auth.getToken()
  if (token) {
    const data = await http('me', {token})
    user = data.user
  }
  return user
}

const AuthContext = React.createContext<
  {
      user: User | null;
      login: (formData: AuthForm) => Promise<void>;
      register: (formData: AuthForm) => Promise<void>;
      logout: () => Promise<void>;
  }
  | undefined
>(undefined);
AuthContext.displayName = "AuthContext";

export const AuthProvider = ({ children }: { children: ReactNode }) => {
  const queryClient = useQueryClient()
  // const [user, setUser] = useState<User | null>(null);
  const {run, isIdle, isLoading, data: user, setData: setUser} = useAsync<User | null>()

  const login = (FormData: AuthForm) => auth.login(FormData).then(setUser);
  const register = (FormData: AuthForm) => auth.register(FormData).then(setUser);
  const logout = () => auth.logout().then(() => {
    setUser(null)
    queryClient.clear() // 清空缓存
    window.location.href = window.location.origin
  });

  useMount(() => {
    run(bootstrapUser())
  })

  // 如果正在获取初始化user渲染加载页面
  if (isIdle || isLoading) {
    return <FullPageLoading/>
  }
  

  return (
    <AuthContext.Provider
      children={children}
      value={{ user, login, register, logout }}
    />
  );
};

export const useAuth = () => {
  const context = React.useContext(AuthContext);
  if (!context) {
    throw new Error("useAuth必须在AuthProvider中使用",);
  }
  return context;
};
