import {Drawer, Button, Spin, Form, Input} from 'antd'
import { useAddProject, useEditProject, useProjectModal } from 'utils/project'
import {IdSelect} from 'components/IdSelect'
import { useUsers } from 'utils/user'
import styled from '@emotion/styled'
import { useForm } from 'antd/lib/form/Form'
import { useEffect } from 'react'

export const ProjectModal = () => {

  const {isOpen, close, editProject, isLoading } = useProjectModal()

  const {data: users} = useUsers()

  const title = editProject ? '编辑项目' : '创建项目'
  const useMutate = editProject ? useEditProject : useAddProject

  const {mutateAsync, isLoading: mutateLoading} = useMutate()

  const [form] = useForm() // 用来操作表单

  useEffect(() => {
    form.setFieldsValue(editProject)
    return () => {
      form.resetFields()
    }
  }, [editProject, form])

  const onFinish = (values: any) => {
    
    mutateAsync({...editProject, ...values}).then(res => {
      form.resetFields()
      close()
    })
  }

  return <Drawer
    width={'100%'}
    visible={isOpen}
    onClose={close}
    forceRender={true}
  >
    <Container>
    {
      isLoading ? (
        <Spin size={'large'}/>
      ) : (
        <>
          <h1>{title}</h1>
          <Form onFinish={onFinish}
          form={form}
          layout={"vertical"}
          style={{ width: "40rem" }}
          >
            <Form.Item label={'项目名'} key={'name'} name={'name'}
              rules={[{required: true, message: '项目名不能为空'  }]}
            >
              <Input placeholder={'请输入项目名'} />
            </Form.Item>
            <Form.Item label={'部门名'} key={'organization'} name={'organization'}
              rules={[{required: true, message: '部门名不能为空'  }]}
            >
              <Input placeholder={'请输入部门名'} />
            </Form.Item>
            <Form.Item label={'负责人'} key={'personId'} name={'personId'}>
              <IdSelect
                options={users || []}
                defaultOptionName={'负责人'}
              >
              </IdSelect>
            </Form.Item>
            <Form.Item style={{textAlign: 'center'}}>
              <Button loading={mutateLoading} type={'primary'} htmlType={'submit'}>提交</Button>
            </Form.Item>
          </Form>
        </>
      )
    }
    </Container>
  </Drawer>
}

const Container = styled.div`
  height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

`