import styled from '@emotion/styled';
import {Popover, Typography, List, Divider} from 'antd'
import {NoPaddingBtn} from 'components/lib'
import { useProjectModal, useProjects } from 'utils/project';


export const ProjectPopover = () => {

  const {open} = useProjectModal()

  const {data: projects} = useProjects()

  const pinProjects = projects?.filter(project => project.pin)

  const content = <Container>
    <Typography.Text type={"secondary"}>收藏项目</Typography.Text>
      <List>
        {
          pinProjects?.map(project => (
            <List.Item key={project.id}>
              <List.Item.Meta  title={project.name}/>
            </List.Item>
          ))
        }
      </List>
      <Divider/>
      <NoPaddingBtn
        onClick={open}
        type={'link'}>
         创建项目
      </NoPaddingBtn>
  </Container>

  return (
    // onVisibleChange={() => alert('onVisibleChange')}
    <Popover
      placement={"bottom"}
      content={content}
    >
      <span>项目</span>
    </Popover>
  );
}

const Container = styled.div`
  min-width: 30rem;
`