import { useState } from "react"
import { LoginScreen } from "./login"
import { RegisterScreen } from "./register"
import {Button, Card, Divider, Typography} from 'antd';
import styled from '@emotion/styled'
import logo from 'assets/logo.svg'
import left from 'assets/left.svg'
import right from 'assets/right.svg'

export const UnAuthenticatedApp = () => {
  const [isLogin, setIsLogin] = useState<boolean>(true)

  const [error, setError] = useState<Error | null>(null)

  return (
    <Container>
      <Background/>
      <Header />
      <ShadowCard>
        <Title>
          { isLogin ? '登陆' : '注册' }
        </Title>
        { error ? <Typography.Text type={'danger'}>{error.message}</Typography.Text> : null }
        {isLogin ? <LoginScreen onError={setError} /> : <RegisterScreen onError={setError} />}
        <Divider/>
        <Button type={'link'} onClick={() => setIsLogin(!isLogin)}>
          {isLogin ? "没有账号？去注册" : "已有账号？去登录"}
        </Button>
      </ShadowCard>
    </Container>
  );
}

export const LongButton = styled(Button)`
  width: 100%;
`

const Title = styled.h2`
  margin-bottom: 2.4rem;
  color: rgb(94, 108, 132);
`;

const Background = styled.div`
  position: absolute;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat; // 使背景图片不重复
  background-attachment: fixed;
  background-position: left bottom, right bottom; // 用逗号分开两张图片的样式
  background-size: calc(((100vw - 40rem) / 2) - 3.2rem),
    calc(((100vw - 40rem) / 2) - 3.2rem), cover; // cover平铺
  background-image: url(${left}), url(${right});
`;

const Header = styled.header`
  background: url(${logo}) no-repeat center;
  padding: 5rem;
  width: 100%;
  background-size: 8rem;
`

const ShadowCard = styled(Card)`
  width: 40rem;
  min-height: 56rem;
  padding: 3.2rem 4rem;
  border-radius: 0.3rem;
  box-sizing: border-box;
  box-shadow: rgba(0, 0, 0, 0.1) 0 0 10px;
  text-align: center;
`;

const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
`;