import { useCallback, useState } from "react"
import { useMountedRef } from "utils"

interface State<V> {
  error: Error | null;
  data: V | null;
  stat: 'idle' | 'loading' | 'success' | 'error';
}

const defaultInitialState: State<null> = {
  error: null,
  data: null,
  stat: 'idle'
}
/**
 * 泛型为返回数据类型
 * @param initialState
 * @returns {isIdle, isLoading, isSuccess, isError, run, ...state}
 */
export const useAsync = <V>(initialState?: State<V>) => {

  const mountedRef = useMountedRef()

  const [state, setState] = useState<State<V>>({
    ...defaultInitialState,
    ...initialState
  })
// 异步请求成功调用
  const setData = useCallback(
    (data: V) => {
      setState({
        error: null,
        data,
        stat: 'success'
      })
    },
    [],
  )
// 请求失败调用
  const setError = useCallback(
    (error: Error) => {
      setState({
        error,
        data: null,
        stat: 'error'
      })
    },
    [],
  )

  // run 用来触发异步请求
  const run = useCallback(
    (promise: Promise<V>) => {
      // 当传入的不是promise抛出错误
      if (!promise || !promise.then) {
        throw new Error('请传入一个Promise')
      }
      // 设置状态为loading
      setState((prev) => ({
        ...prev,
        stat: 'loading'
      }))
  
      return promise
      .then(res => {
        // 数据请求成功
      if (mountedRef.current)
          setData(res)
        return res
      })
      .catch(error => {
        // 请求失败
        if (mountedRef.current)
        setError(error)
        // return error catch中异常会被消化不会返回，如在外面需要获取异常需抛出异常，如Promise.reject
        return Promise.reject(error)
      })
    },
    [mountedRef, setData, setError],
  )

  return {
    isIdle: state.stat === 'idle',
    isLoading: state.stat === 'loading',
    isSuccess: state.stat === 'success',
    isError: state.stat === 'error',
    run,
    setData,
    setError,
    setState,
    ...state
  }
}