import {ColumnContainer} from 'components/lib'
import {Input} from 'antd'
import { useAddSpectacular } from './utils'
import { useState } from 'react'

export const CreateSpect = () => {

  const {mutateAsync} = useAddSpectacular()

  const [name, setName] = useState<string>('')

  const submit = () => {
    mutateAsync({name}).then(res =>  setName(''))
  }


  return <ColumnContainer>
    <Input
     onPressEnter={() => submit()} 
     placeholder={'新建看板'}
     value={name}
     onChange={(evt) => setName(evt.target.value)}
      />
  </ColumnContainer>
}