import { Spin } from "antd";
import { useDocumentTitle } from "utils";
import { useProjectInUrl, useReorderKanban, useReorderTask, useSpectacular, useTaskModal, useTasks } from "./utils";
import { KanbanColumn } from "components/KanbanColumn";
import { SearchPanel } from "./SearchPanel";
import styled from "@emotion/styled";
import {CreateSpect} from './CreateSpect'
import {TaskModal} from './TaskModal'
import { DragDropContext, DropResult } from "react-beautiful-dnd";
import { Drag, Drop, DropChild } from "components/DragAndDrop";
import { useCallback } from "react";

export const SpectacularsScreen = () => {
  useDocumentTitle("看板列表");

  const { data: currentProject, isLoading: ProjectLoading } = useProjectInUrl();
  const {
    data: spectaculars,
    isLoading: spectacularsLoading,
  } = useSpectacular();

  const {editingTask} = useTaskModal()

  const onDragEnd = useDragEnd()

  return (
    <DragDropContext onDragEnd={onDragEnd}>
      <PageContainer>
          { 
          Number(editingTask) ? 
          <TaskModal/> : null
          }
          <h1>{currentProject?.name}看板</h1>
          <SearchPanel />

          {spectacularsLoading || ProjectLoading ? (
            <Spin size={"large"} />
          ) : (
              <ColumnContainer>
            <Drop type={'COLUMN'} direction={'horizontal'} droppableId={'kanban'}>
              <DropChild style={{display: 'flex'}}>
              {spectaculars?.map((spectacular, index) => (
                <Drag key={spectacular.id} draggableId={String(spectacular.id)} index={index}>
                  <KanbanColumn key={spectacular.id} kanban={spectacular} />
                </Drag>
              ))}
              </DropChild>
            </Drop>
              <CreateSpect/>
            </ColumnContainer>
          )}
        </PageContainer>
    </DragDropContext>
  );
};

export const useDragEnd = () => {

  const {data: kanbans} = useSpectacular()

  const {data: allTasks = []} = useTasks()

  const {mutate: reorderKanban} = useReorderKanban()

  const {mutate: reorderTask} = useReorderTask()
  return useCallback(
    ({source, destination, type}: DropResult) => {
      if (!destination) {
        return
      }
      if (type === 'COLUMN') {
        const fromId = kanbans?.[source.index].id
        const toId = kanbans?.[destination.index].id
        if (!fromId || !toId || fromId === toId) {
          return 
        }
        const type = destination.index > source.index ? 'after' : 'before'
        reorderKanban({fromId, referenceId: toId, type})
      }
      if (type === 'ROW') {
        const fromKanbanId = +source.droppableId
        const toKanbanId = +destination.droppableId
        // if (fromKanbanId === toKanbanId) {
        //   return 
        // }
        const fromTask = allTasks?.filter(task => task.kanbanId === fromKanbanId)[source.index]
        const toTask = allTasks?.filter(task => task.kanbanId === toKanbanId)[destination.index]
        if (fromTask?.id === toTask?.id) {
          return 
        }
        reorderTask({
          fromId: fromTask?.id,
          referenceId: toTask?.id,
          fromKanbanId,
          toKanbanId,
          type: fromKanbanId === toKanbanId && destination.index > source.index ? 'after' : 'before'
        })
      }
    },
    [allTasks, kanbans, reorderKanban, reorderTask],
  )
}

const PageContainer = styled.div`
  display: flex;
  flex-direction: column;
  overflow: hidden;
`;

const ColumnContainer = styled.div`
  user-select: none;
  display: flex;
  overflow-x: scroll;
  ::-webkit-scrollbar {
    display: none;
  }
  flex: 1;
  margin-right: 2rem;
`;
