import { useAuth } from "context/AuthContext";
import { Form, Input } from "antd";
import { LongButton } from "./index";
import { useAsync } from "utils/useAsync";

export const RegisterScreen = ({
  onError,
}: {
  onError: (error: Error) => void;
}) => {
  const { register } = useAuth();
  const { run, isLoading } = useAsync();

  const handleRegister = ({cpassword, ...value}: { username: string; password: string, cpassword: string }) => {
    if (cpassword !== value.password) {
      onError(new Error('两次输入密码不一致'))
      return 
    }
    run(register(value)).catch((error) => onError(error));
  };

  return (
    <Form onFinish={handleRegister}>
      <Form.Item
        name={"username"}
        rules={[{ required: true, message: "请输入用户名" }]}
      >
        <Input placeholder={"请输入用户名"} type={"text"} id={"username"} />
      </Form.Item>
      <Form.Item
        name={"password"}
        rules={[{ required: true, message: "请输入密码" }]}
      >
        <Input placeholder={"请输入密码"} type={"password"} id={"password"} />
      </Form.Item>
      <Form.Item
        name={"cpassword"}
        rules={[{ required: true, message: "请确认密码" }]}
      >
        <Input placeholder={"确认密码"} type={"password"} id={"cpassword"} />
      </Form.Item>
      <LongButton loading={isLoading} htmlType={"submit"} type={"primary"}>
        注册
      </LongButton>
    </Form>
  );
};
