import { useQueryClient } from 'react-query';
// import { useEffect } from "react";
import { Project } from "types";
import { cleanObject } from "utils";
import { useHttp } from "./http";
import { useSetUrlSearchParam, useUrlQueryParam } from "./url";
import {useMutation, useQuery} from 'react-query'

let key: Partial<Project> = {}

export const useProjects = (params?: Partial<Project>) => {
  key = cleanObject(params) || {} // 缓存名， 如果再次请求参数都一样则使用本地缓存
  const client = useHttp();
//                定义返回值类型W
  return useQuery<Project[], Error>(['projects', key], 
   () => client("projects", { data: cleanObject(params|| {}) }))
};

export const useEditProject = () => {
  // const { run, ...result } = useAsync<Project[]>();
  
  const client = useHttp();
  const queryClient = useQueryClient() // 用来操作存储在浏览器中的缓存
  const queryKey = ['projects', key] // 缓存名

  return useMutation( // 返回一个包含 mutate和mutateAsync的对象
    (data: Partial<Project>) => {
      
      return client(`projects/${data.id}`, { data, method: "PATCH" })
    }, {
      onSuccess: () => queryClient.invalidateQueries(queryKey),
      async onMutate(target: Partial<Project>) { // target为发送请求修改的值
        const previousItems = queryClient.getQueryData(queryKey) // 用来回滚
        
        queryClient.setQueryData(queryKey, (old?: Project[]) => {
          
          return old?.map(project => project.id === target.id ? {...project, ...target} : project) || []
        })
        return {previousItems}
      },
      onError: (err, newItem, context) => { // context可以获取到onMutate中返回的变量
        // 如果该请求最终出错了，就把缓存回滚到乐观更新之前的
        queryClient.setQueryData(queryKey, (context as {previousItems: Project[]}).previousItems)
      }
    })
};

export const useAddProject = () => {
  const client = useHttp();

  const queryClient = useQueryClient()

  return useMutation(
    (data: Partial<Project>) => {
      return client('projects', { method: 'POST', data })
    }, {
      onSuccess: () => queryClient.invalidateQueries('projects')
    }
  )
};

export const useDeleteProject = () => {
  const client = useHttp()
  
  const queryClient = useQueryClient()

  return useMutation(
    (id: number) => client(`projects/${id}`, {method: 'DELETE'}),
    {
      // 刷新projects缓存
      onSuccess: () => queryClient.invalidateQueries('projects')
    }
  )
}

export const useProject = (id?: number) => {
  const client = useHttp()

  return useQuery(['project', id], () => client(`projects/${id}`),
    {
      enabled: Boolean(id)
    }
  )
}

export const useProjectModal = () => {
  const [{ projectCreated }, setUrlQueryParam] = useUrlQueryParam([
    "projectCreated",
  ]);

  const [{editingProjectId}, setEditingProjectId] = useUrlQueryParam(['editingProjectId'])

  const {data: editProject, isLoading} = useProject(Number(editingProjectId))

  const setUrlParams = useSetUrlSearchParam()

  const startEdit = (projectId: number) => {
    setEditingProjectId({editingProjectId: projectId})
  }

  const open = () => {
    setUrlQueryParam({ projectCreated: true });
  };
  const close = () => {
    setUrlParams({ projectCreated: undefined, editingProjectId: undefined })
  };

  return {
    isOpen: projectCreated === "true" || Boolean(editingProjectId),
    open,
    close,
    startEdit,
    editProject,
    isLoading
  };
};