import { User } from "types";
import { Dropdown, Table, TableProps, Menu } from "antd";
import dayjs from "dayjs";
import { Link } from "react-router-dom";
import { Pin } from "components/Pin";
import { useDeleteProject, useEditProject, useProjectModal } from "utils/project";
import { Project } from "types";
import confirm from 'antd/lib/modal/confirm';

// interface ListProps {
//   list: Project[];
//   users: User[];
// }

interface PropsType extends TableProps<Project> {
  users: User[];
}

export const List = ({ users, ...props }: PropsType) => {

  const {mutate} = useEditProject()
  const {mutate: deleteMutate} = useDeleteProject()

  const pinProject = (id: number) => (pin: boolean) => {
    
    mutate({id, pin})
  }

  const {startEdit} = useProjectModal()

  const onDelete = (id: number) => {
    // closeTaskModal()
      confirm({
      okText: "确定",
      cancelText: "取消",
      title: "确定删除任务吗",
      onOk: () => {
        // return deleteTask({ id: Number(editingTaskId) });
        deleteMutate(id)
      },
    });
  }

  return (
    <Table
      rowKey={"id"} //rowKey用来定义渲染的每一项的key
      pagination={false}
      columns={[
        {
          title: <Pin checked={true} disabled={true} />,
          render: (value, project) => <Pin
           checked={project.pin} 
           onCheckChange={ pinProject(project.id) } />,
        },
        {
          title: "项目名",
          render: (value, project) => {
            // value和project值一样

            return <Link to={project.id + ""}>{project.name}</Link>;
          },
          // 中文排序
          sorter: (a, b) => a.name.localeCompare(b.name),
        },
        {
          title: "部门",
          dataIndex: "organization",
        },
        {
          title: "负责人",
          render(value, project) {
            return (
              <span>
                {users.find((user) => user.id === project.personId)?.name ||
                  "未知"}
              </span>
            );
          },
        },
        {
          title: "创建时间",
          render: (value, project) => {
            return (
              <span>
                {project.created
                  ? dayjs(project.created).format("YYYY-MM-DD")
                  : "未知"}
              </span>
            );
          },
        },
        {
          render: (value, project) => {
            return (
              <Dropdown
                overlay={
                  <Menu>
                    <Menu.Item key={"logout"} onClick={() => startEdit(project.id)}>编辑</Menu.Item>
                    <Menu.Item key={"delete"} onClick={() => onDelete(project.id)}>删除</Menu.Item>
                  </Menu>
                }
              >
                <span>···</span>
              </Dropdown>
            );
          }
        }
      ]}
      {...props}
    />
  );
};
