import {Card} from 'antd'
import { Task } from 'types'
import {TypeIcon} from 'components/KanbanColumn'
import { useSearchUrlParams, useTaskModal } from 'screens/spectaculars/utils'
import {Mark} from 'components/Mark'
// import { useDebounce } from 'utils'

export const TaskCard = ({task}: {task: Task}) => {

  const { editTaskById} = useTaskModal()

  const {searchParams} = useSearchUrlParams()
  // const debounceName = useDebounce(searchParams.name, 500)

  return <Card
  onClick={() => editTaskById(task.id)}
  style={{marginBottom: '0.5rem', cursor: 'pointer'}}
  key={task.id}>
    <Mark name={task.name} keyword={searchParams.name} />
    <TypeIcon typeId={task.typeId} />
  </Card>
}