import {Rate} from 'antd'
import React from 'react'
type RateProps = React.ComponentProps<typeof Rate>

interface PinProps extends RateProps {
  checked: boolean;
  onCheckChange?: (isChecked: boolean) => void
}


export const Pin = ({checked, onCheckChange, ...restProps}: PinProps) => {
  // count 为点亮的星星数量
  return <Rate
    count={1}
    value={checked ? 1 : 0}
    onChange={v => onCheckChange?.(Boolean(v))}
    {...restProps}
  />
}