import {Form, Input, Button} from 'antd'
import { useForm } from 'antd/lib/form/Form';
import Modal from "antd/lib/modal/Modal"
import { useUsers } from 'utils/user';
import { IdSelect } from "components/IdSelect"
import { useDeleteTask, useEditTask, useTask, useTaskModal, useTaskTypes } from "./utils";
import { useEffect } from 'react';
import confirm from 'antd/lib/modal/confirm';

export const TaskModal = () => {
  const layout = {
        labelCol: { span: 8 },
        wrapperCol: { span: 16 },
      };
  
  const {editingTask: editingTaskId, closeTaskModal} = useTaskModal()
  const {data: editingTask} = useTask(Number(editingTaskId))

  const [form] = useForm()

  const {data: users} = useUsers()

  const {data: TaskTypes} = useTaskTypes()

  const {mutateAsync: editMutate, isLoading} = useEditTask()

  const {mutateAsync: deleteMutate} = useDeleteTask()

  useEffect(() => {
    form.setFieldsValue(editingTask)
  }, [editingTask, editingTaskId, form])

  const onCancel = () => {
    closeTaskModal()
    form.resetFields()
  }
  const onOk = async () => {
    await editMutate({...form.getFieldsValue(), id: Number(editingTaskId)})
    form.resetFields()
    closeTaskModal()
  }
  
  const onDelete = () => {
    closeTaskModal()
      confirm({
      okText: "确定",
      cancelText: "取消",
      title: "确定删除任务吗",
      onOk: () => {
        // return deleteTask({ id: Number(editingTaskId) });
        deleteMutate(Number(editingTaskId)).then(() => {
          onCancel()
        })
        
      },
    });
  }
    
    
  return <Modal
  forceRender={true}
    onCancel={onCancel}
    onOk={onOk}
    okText={"确认"}
    cancelText={"取消"}
    confirmLoading={isLoading}
    title={"编辑任务"}
    visible={!!editingTaskId}
  >
    <Form {...layout} initialValues={editingTask} form={form}>
     <Form.Item
      label={"任务名"}
      name={"name"}
      rules={[{ required: true, message: "请输入任务名" }]}
    >
      <Input />
    </Form.Item>
    <Form.Item label={"经办人"} name={"processorId"} key={'processorId'}>
      <IdSelect options={users} defaultOptionName={"经办人"} />
    </Form.Item>
    <Form.Item label={"类型"} name={"typeId"} key={'typeId'}>
      <IdSelect options={TaskTypes} defaultOptionName={'类型'} />
    </Form.Item>
  </Form>
  <div style={{ textAlign: "right" }}>
     <Button
     danger
      type={'ghost'}
      onClick={() => onDelete()}
      style={{ fontSize: "14px" }}
      size={"small"}
    >
      删除
    </Button>
  </div>
  </Modal>
}