import {SearchPanel} from './SearchPanel';
import {List} from './List'
import {useMemo} from 'react';
import styled from '@emotion/styled';
import { Button, Typography } from 'antd';
import { useProjectModal, useProjects } from 'utils/project';
import { useUsers } from 'utils/user';
import { useUrlQueryParam } from 'utils/url';
import {toNumber} from 'components/IdSelect'
import { Row } from 'components/lib';
import { useDebounce } from 'utils';

export const ProjectListScreen = () => {

  const [param, setParam] = useUrlQueryParam(['name', 'personId'])

  const projectsParam = useMemo(() => {
    return { ...param, personId: toNumber(param.personId) || undefined}
  }, [param])

  const {isLoading, error, data: list} = useProjects(useDebounce(projectsParam, 300))
  const {data: users} = useUsers()

  const {open} = useProjectModal()

  return <Container>
    <Row between={true}>
      <h1>项目列表</h1>
      <Button onClick={open} type={'link'}>创建项目</Button>
    </Row>
    <SearchPanel param={projectsParam} setParam={setParam} users={users || []}/>
    { error ? <Typography.Text type={'danger'}>{error.message}</Typography.Text> : null}
    <List loading={isLoading} dataSource={list || []} users={users || []} />
  </Container>
}

const Container = styled.div`
  padding: 3.2rem;
  display: flex;
  flex-direction: column;
  flex: 1;
`