import {Link} from 'react-router-dom'
import {Routes, Route, Navigate, useLocation} from 'react-router'
import {SpectacularsScreen} from 'screens/spectaculars'
import {EpicScreen} from 'screens/epic'
import styled from '@emotion/styled'
import { Menu} from 'antd'

export const ProjectScreen = () => {

  const useUrlType = () => {
    const {pathname} = useLocation()
    return pathname.split('/').slice(-1)
    
  }

  return <ScreenContainer>
      <Aside>
        <Menu mode={'inline'} selectedKeys={useUrlType()}>
          <Menu.Item key={'spectaculars'}>
            <Link to={'spectaculars'}>看板</Link>
          </Menu.Item>
          <Menu.Item key={'epic'}>
            <Link to={'epic'}>任务组</Link>
          </Menu.Item>
        </Menu>
      </Aside>
      <Main>
      <Routes>
        <Route path={'/spectaculars'} element={<SpectacularsScreen />} />
        <Route path={'/epic'} element={<EpicScreen />} />
        <Navigate to={'spectaculars'} replace={true} />
      </Routes>
      </Main>
  </ScreenContainer>
}

const ScreenContainer = styled.div`
  /* padding: 3.2rem; */
  display: grid;
  grid-template-columns: 16rem 1fr;
  flex: 1;
`

const Aside = styled.aside`
  background-color: rgb(244, 245, 247);
`

const Main = styled.main`
  padding: 3.2rem;
  box-shadow: -5px 0 5px -5px rgba(0, 0, 0, 0.1);
  display: flex;
  overflow: hidden;
`