import { Input, Button } from "antd"
import { IdSelect } from "components/IdSelect"
import { Row } from "components/lib"
import { useUsers } from "utils/user"
import { useSearchUrlParams, useTaskTypes } from "./utils"

export const SearchPanel = () => {

  const {data: users} = useUsers()
  const {data: taskType} = useTaskTypes()

  const {searchParams, setSearchParams, reset} = useSearchUrlParams()
  
  return <Row gap={true} marginBottom={4}>
    <Input style={{width: '20rem'}} placeholder={'任务名'}
      value={searchParams.name}
      onChange={evt => setSearchParams({name: evt.target.value})
      }
    />
    <IdSelect options={users} defaultOptionName={'经办人'}
      value={searchParams.processorId}
      onChange={(value) => setSearchParams({processorId: value})}
     />
    <IdSelect options={taskType} defaultOptionName={'类型'}
      value={searchParams.typeId}
      onChange={value => setSearchParams({typeId: value}) }
    />
    <Button onClick={() => reset()} >清空搜索</Button>
  </Row>
}