import { useEffect, useRef, useState } from "react";

// 当value为0时不为假
export const isFalsy = (value: unknown): boolean =>
  value === 0 ? false : !value;

/**
 * 解决isFalsy 对值为false的属性也会删除
 * @param value
 * @returns isVoid是否需要规避的
 */
const isVoid = (value: unknown): boolean =>
  value === undefined || value === null || value === "";

/**
 * 清除object中value为空的属性
 * @param object
 * @returns object
 */
export const cleanObject = (object?: { [key: string]: unknown }) => {
  if (!object) {
    return {};
  }
  const result = { ...object };
  Object.keys(result).forEach((key) => {
    const value = result[key];
    if (isVoid(value)) {
      delete result[key];
    }
  });
  return result;
};

/**
 * 模拟componentDidMount
 * @param {() => void} callback
 */
export const useMount = (callback: () => void) => {
  useEffect(() => {
    callback();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
};

/**
 *
 * @param {V} value 需要进行防抖的值
 * @param {number} delay 延迟时间
 * @returns {V} debounceValue
 */
export const useDebounce = <V>(value: V, delay?: number) => {
  const [debounceValue, setDebounceValue] = useState(value);

  useEffect(() => {
    // 设置一个计时器
    const timeout = setTimeout(() => {
      setDebounceValue(value);
    }, delay);

    // 清除前一个useEffect中设置的timeout
    return () => clearTimeout(timeout);
  }, [value, delay]);

  return debounceValue;
};

export const useDocumentTitle = (
  title: string,
  keepOnUnmounted: boolean = false
) => {
  const oldTitle = useRef(document.title).current;

  useEffect(() => {
    document.title = title;

    return () => {
      document.title = oldTitle
    }
  }, [oldTitle, title]);

  // useEffect(() => {
  //   return () => {
  //     if (!keepOnUnmounted) {
  //       document.title = oldTitle;
  //     }
  //   };
  // }, [oldTitle, keepOnUnmounted]);
};

export const resetHref = () => (window.location.href = window.location.origin);

/**
 * 传入一个对象，和键集合，返回对应的对象中的键值对
 * @param obj
 * @param keys
 */
export const subset = <
  O extends { [key in string]: unknown },
  K extends keyof O
>(
  obj: O,
  keys: K[]
) => {
  const filteredEntries = Object.entries(obj).filter(([key]) =>
    keys.includes(key as K)
  );
  return Object.fromEntries(filteredEntries) as Pick<O, K>;
};

export const useMountedRef = () => {
  const mountedRef = useRef<boolean>(false);

  useEffect(() => {
    mountedRef.current = true;
    return () => {
      mountedRef.current = false;
    };
  });

  return mountedRef;
};
