import { useDeleteSpectacular, useSearchUrlParams, useTasks } from 'screens/spectaculars/utils'
import { Spectacular } from 'types'
import taskIcon from 'assets/task.svg'
import bugIcon from 'assets/bug.svg'
import styled from '@emotion/styled'
import { useDebounce } from 'utils'
import {ColumnContainer} from 'components/lib'
import {CreateTask} from 'screens/spectaculars/CreateTask'
import {TaskCard} from 'components/TaskCard'
import  {Row} from 'components/lib'
import {Dropdown, Menu } from 'antd'
import confirm from 'antd/lib/modal/confirm';
import React from 'react'
import { Drag, Drop, DropChild } from './DragAndDrop'


export const KanbanColumn = React.forwardRef<HTMLDivElement, {kanban: Spectacular}>(({kanban, ...props}, ref) => {

  const {searchParams} = useSearchUrlParams()
  const {data: tasks } = useTasks(useDebounce(searchParams, 300))
  const currentTasks = tasks?.filter(task => task.kanbanId === kanban.id)

  const {mutateAsync: deleteMutate} = useDeleteSpectacular()

  const onDelete = (id: number) => {
    confirm({
      okText: "确定",
      cancelText: "取消",
      title: "确定删除看板吗",
      onOk: () => {
        deleteMutate(id)
      },
    });
  }

  return <ColumnContainer {...props} ref={ref} >
      <Row between={true}>
        <h3 key={kanban.id}>{kanban.name}</h3>
        <Dropdown
          overlay={
            <Menu>
              <Menu.Item key={"delete"} onClick={() => onDelete(kanban.id)}>删除</Menu.Item>
            </Menu>
          }
        >
          <span>···</span>
        </Dropdown>
      </Row>
      <TaskContainer>
          <Drop type={'ROW'} direction={'vertical'} droppableId={'' + kanban.id}>
            <DropChild>
            {
              currentTasks?.map((task, taskIndex) => (
                <Drag key={task.id} index={taskIndex} draggableId={'' + task.id}>
                  <div>
                    <TaskCard key={task.id} task={task} />
                  </div>
                </Drag>
              ))
            }
            </DropChild>
          </Drop>
      <CreateTask kanbanId={kanban.id}/>
      </TaskContainer>

  </ColumnContainer>
})

const TaskContainer = styled.div`
overflow: scroll;
flex: 1;
::-webkit-scrollbar {
  display: none;
}
`

export const TypeIcon = ({typeId}: {typeId: number}) => {
  return <img src={typeId === 7 ? taskIcon : bugIcon} alt="typeIcon"/>
}