import { useMemo } from 'react';
import { Task, TaskType } from './../../types/index';
import { useLocation } from "react-router"
import { useHttp } from "utils/http"
import {useMutation, useQuery, useQueryClient} from 'react-query'
import { Spectacular } from 'types'
import { useProject } from "utils/project"
import { useUrlQueryParam } from 'utils/url';

export const useUrlProjectId = () => {
  const {pathname} = useLocation()

  const id = pathname.match(/projects\/(\d+)/)?.[1]
  
  return Number(id)
}

export const useProjectInUrl = () => useProject(useUrlProjectId())

export const useSpectacular = (param?: Partial<Spectacular>) => {
  const client = useHttp()

  const data = {projectId: useUrlProjectId(), ...param}
  return useQuery<Spectacular[]>(['spectacular', data], () => {
    return client(`kanbans`, {data})
  })
}

export const useTasks = (param?: Partial<Task>) => {
  const client = useHttp()

  const data = {projectId: useUrlProjectId(), ...param}
  return useQuery<Task[]>(['tasks', data], () => {
    return client(`tasks`, {data})
  })
}

export const useTaskTypes = () => {
  const client = useHttp()

  return useQuery<TaskType[]>(['taskTypes'], () => (
    client('taskTypes')
  ))
}

export const useSearchUrlParams = () => {

  const [params, setSearchParams] = useUrlQueryParam(['name', 'typeId', 'tagId', 'processorId'])

  const reset = () => {
    setSearchParams({
      name: undefined,
      typeId: undefined,
      tagId: undefined,
      processorId: undefined
    })
  }

  const projectId = useUrlProjectId()

  const searchParams = useMemo(() => ({
    projectId,
    typeId: Number(params.typeId) || undefined,
    tagId: Number(params.tagId) || undefined,
    processorId: Number(params.processorId) || undefined,
    name: params.name
  }), [params.name, params.processorId, params.tagId, params.typeId, projectId])

  return {
    searchParams,
    reset,
    setSearchParams
  }
}

export const useAddSpectacular = () => {
  const client = useHttp()

  const projectId = useUrlProjectId()

  const queryClient = useQueryClient()

  return useMutation(
    (params: Partial<Spectacular>) => {
      return client('kanbans', {data: {projectId, ...params}, method: 'POST'})
    },
    {
      onSuccess: () => {
        queryClient.invalidateQueries('spectacular')
      }
    }
  )
}

export const useDeleteSpectacular = () => {
  const client = useHttp()

  const queryClient = useQueryClient()

  return useMutation(
    (id: number) => client(`kanbans/${id}`, {method: 'DELETE'}),
    {
      onSuccess: () => {
        queryClient.invalidateQueries('spectacular')
      }
    }
  )
}

export const useEditSpectacular = () => {
  const client = useHttp()

  const queryClient = useQueryClient()

  return useMutation(
    (data: Partial<Spectacular>) => client(`kanbans/${data.id}`, { data, method: 'PATCH'}),
    {
      onSuccess: () => {
        queryClient.invalidateQueries('spectacular')
      }
    }
  )
}

export const useAddTask = () => {
  const client = useHttp()
  const projectId = useUrlProjectId()
  const queryClient = useQueryClient()


  return useMutation(
    (params: Partial<Task>) => (
      client('tasks', {data:{projectId, ...params}, method:'POST' })
    ),
    {
      onSuccess: () => {
        queryClient.invalidateQueries('tasks')
      }
    }
  )
}

export const useTaskModal = () => {

  const [{editingTask}, setEditingTask] = useUrlQueryParam(['editingTask'])

  const editTaskById = (id: number) => {
    setEditingTask({editingTask: id})
  }

  const closeTaskModal = () => {
    setEditingTask({editingTask: undefined})
  }

  return {
    editingTask: Number(editingTask) || undefined,
    editTaskById,
    closeTaskModal
  }
}

export const useTask = (id: number) => {
  const client = useHttp()
  return useQuery<Task>(['tasks', id], () => client(`tasks/${id}`))
}

export const useEditTask = () => {
  const client = useHttp()

  const queryClient = useQueryClient()

  return useMutation(
    (data: Partial<Task>) => client(`tasks/${data.id}`, {method: 'PATCH', data}),
    {
      onSuccess: () => {
        queryClient.invalidateQueries('tasks')
      }
    }
  )
}

export const useDeleteTask = () => {

  const client = useHttp()

  const queryClient = useQueryClient()

  return useMutation(
    (id: number) => client(`tasks/${id}`, {method: 'DELETE'}),
    {
      onSuccess: () => {
        queryClient.invalidateQueries('tasks')
      }
    }
  )
}

export interface SortProps {
  // 要重新排序的 item
  fromId: number;
  // 目标item
  referenceId: number;
  // 放在目标item的前还是后
  type: "before" | "after";
  fromKanbanId?: number;
  toKanbanId?: number;
}

export const useReorderKanban = () => {
  const client = useHttp()

  const queryClient = useQueryClient()

  return useMutation(
    (params: SortProps) => client('kanbans/reorder', {
      data: params,
      method: 'POST'
    }),
    {
      onSuccess: () => {
        queryClient.invalidateQueries('spectacular')
      }
    }
  )
}

export const useReorderTask = () => {
  const client = useHttp()

  const queryClient = useQueryClient()

  return useMutation(
    (params: SortProps) => client('tasks/reorder', {
      data: params,
      method: 'POST'
    }),
    {
      onSuccess: () => {
        queryClient.invalidateQueries('tasks')
      }
    }
  )
}